}

.gradient-border-before::before {
  content: "";
  height: 1px;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-image: linear-gradient(to left, #381f47 30%, rgb(2, 1, 1) 65%);
}

.gradient-border-before-revers::before {
  content: "";
  height: 1px;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-image: linear-gradient(to right, #381f47 30%, rgb(2, 1, 1) 65%);
}

.gradient-border-after::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  z-index: 1;
  background-image: linear-gradient(to right, #381f47 30%, rgb(2, 1, 1) 65%);
}

.gradient-border-after-revers::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  z-index: 1;
  background-image: linear-gradient(to left, #381f47 30%, rgb(2, 1, 1) 65%);
}

