  font-family: "AA Neon", Helvetica, sans-serif;
  font-size: 5rem;
  font-weight: normal;
  line-height: 100%;
  position: relative;
  z-index: 9;
  display: inline;
  margin-bottom: 4rem;
  text-align: center;
  letter-spacing: 0;
  color: rgb(4, 217, 255);
  perspective: 100px;
  filter: drop-shadow(1px 0px 3px rgb(32, 25, 71)) invert(5%);
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .questions__title {
    font-size: calc(24px + 36 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .questions__title {
    font-size: 24px;
  }
}
.questions__list {
  max-width: 1240px;
  padding: 2rem;
  border-radius: 4px;
  backdrop-filter: blur(20px);
  border: 1px solid rgba(128, 128, 128, 0.09);
  box-shadow: 0 0 7px 10px rgba(0, 0, 0, 0.1);
  background: rgba(28, 21, 63, 0.27);
}
.questions__item:not(:last-child) {
  margin-bottom: 2rem;
  border-bottom: 1px solid #4e4e4e;
}
.questions__item._collapsed .questions__collapse {
  grid-template-rows: 1fr;
}
.questions__item._collapsed h3 {
  color: rgb(204, 255, 0);
}
.questions__item._collapsed .icon-angles-down-solid {
  scale: -1;
}
.questions__head {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
}
.questions__head .icon-angles-down-solid {
  transition: scale 0.4s, color 0.4s ease-in-out;
  pointer-events: none;
  padding: 0 2rem;
  text-shadow: 0 0 1px rgb(200, 255, 0);
}
.questions__head .icon-angles-down-solid::before {
  color: rgb(87, 87, 255);
}
.questions__head h3 {
  pointer-events: none;
  font-size: 20px;
  line-height: 125%; /* 75px/28 */
  transition: color 0.4s ease-in-out;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .questions__head h3 {
    font-size: calc(14px + 6 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .questions__head h3 {
    font-size: 14px;
  }
}
.questions__collapse {
  display: grid;
  grid-template-rows: 0fr;
  overflow: hidden;
  transition: grid-template-rows 0.4s ease-in-out;
}
.questions__text {
  min-height: 0;
  font-size: 16px;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .questions__text {
    font-size: calc(12px + 4 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .questions__text {
    font-size: 12px;
  }
}
.questions__text p {
  opacity: 0.7;
  line-height: 155%;
  margin-bottom: 1rem;
}
.questions__text p:last-child {
  margin-bottom: 30px;
}
.questions__text p a {
  display: inline-block;
  white-space: nowrap;
  color: rgb(4, 217, 255);
  text-decoration: underline;
  opacity: 1;
}
.questions__text ul {
  margin-bottom: 2rem;
}
.questions__text li {
  list-style: disc inside;
  opacity: 0.7;
}